.s-services-cards {
    padding: rem(40) 0;

    @media (min-width: 768px) {
        padding: rem(50) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(60) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(75) 0;
    }

    &__items {
        display: grid;
        gap: rem(60) rem(20);
        grid-template-columns: repeat(2, 1fr);

        @media (min-width: 768px) {
            gap: rem(60) rem(30);
            grid-template-columns: repeat(3, 1fr);
        }

        @media (min-width: 1024px) {
            gap: rem(97) rem(30);
        }

        @media (min-width: 1600px) {
            gap: rem(50) rem(30);
            grid-template-columns: repeat(4, 1fr);
        }

        @media (min-width: 1920px) {
            gap: rem(80) rem(30);
        }
    }
}
